/* scss variables */
:root {
  font-size: 16px;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
  margin: 0;
}

body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  color: #363636;
  font-weight: normal;
  background-color: #fff;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body .modal-open {
  margin-right: 0;
}
body.active {
  overflow: hidden;
}

.wrapper {
  min-height: 100%;
  margin-bottom: -50px; /* This needs to be calculated dynamically via js  - height of the footer */
  /* form fields */
}
.wrapper .container {
  height: 100%;
}
.wrapper input,
.wrapper textarea,
.wrapper select {
  font-family: "Montserrat", sans-serif;
  color: #363636;
  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.wrapper select {
  cursor: pointer;
  padding: 9px 6px 9px;
  border: 0;
  border-radius: 0;
  font-size: 20px;
  -webkit-appearance: none;
  height: 36px;
  -moz-appearance: none;
  background: none;
  text-indent: 0.01px;
  color: #363636;
  font-family: "Montserrat", sans-serif;
  text-overflow: "";
}

a {
  color: #363636;
  text-decoration: none;
  cursor: pointer;
  outline: 0;
}

ul {
  padding-left: 0;
}
ul li {
  line-height: 26px;
}

a:hover {
  text-decoration: underline;
}

img {
  border: none 0;
}

input[type=submit] {
  cursor: pointer;
}

input:focus,
textarea:focus {
  outline: transparent none;
}

select:focus {
  outline: none;
}

section {
  display: block;
}

.errors,
.success {
  background: #ffffff;
  display: block;
  font-size: 14px;
  overflow: hidden;
  padding: 0;
}

.errors {
  color: #f04f71;
}

.success {
  color: #51bf9b;
}

/* text selection color */
::selection {
  background: #000;
  color: #fff;
}

::-moz-selection {
  background: #000;
  color: #fff;
}

/* scrollbar color only for windows OS */
.win-os ::-webkit-scrollbar {
  width: 10px;
  height: 5px;
  cursor: pointer;
}
@media only screen and (max-width: 768px) {
  .win-os ::-webkit-scrollbar {
    width: 4px;
  }
}

.win-os ::-webkit-scrollbar-track {
  background-color: #ccc;
}

.win-os ::-webkit-scrollbar-thumb {
  width: 10px;
  background-color: #ececec;
}
@media only screen and (max-width: 768px) {
  .win-os ::-webkit-scrollbar-thumb {
    width: 4px;
  }
}

/* placeholder */
input,
textarea {
  /* hide placeholder on focus of input */
  /* FF 4-18 */
  /* FF 19+ */
  /* IE 10+ */
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #fff;
}
input:-moz-placeholder,
textarea:-moz-placeholder {
  color: #fff;
}
input::-moz-placeholder,
textarea::-moz-placeholder {
  color: #fff;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #fff;
}
input:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder {
  color: transparent !important;
}
input:focus:-moz-placeholder,
textarea:focus:-moz-placeholder {
  color: transparent !important;
}
input:focus::-moz-placeholder,
textarea:focus::-moz-placeholder {
  color: transparent !important;
}
input:focus:-ms-input-placeholder,
textarea:focus:-ms-input-placeholder {
  color: transparent !important;
}

input[type=submit] {
  -webkit-appearance: none;
}

.ios-device * {
  background-attachment: scroll;
}

/* wow default css to avoid flickering */
.wow {
  visibility: hidden;
}
@media only screen and (max-width: 1024px) {
  .wow {
    visibility: visible;
  }
}

/* Site Loader */
.site-loader {
  display: none;
  background: #ffffff;
  height: 100%;
  position: fixed;
  width: 100%;
  z-index: 9999;
  -webkit-transition: width 0.6s cubic-bezier(0.6, 0.04, 0.12, 0.96);
  -moz-transition: width 0.6s cubic-bezier(0.6, 0.04, 0.12, 0.96);
  -ms-transition: width 0.6s cubic-bezier(0.6, 0.04, 0.12, 0.96);
  -o-transition: width 0.6s cubic-bezier(0.6, 0.04, 0.12, 0.96);
  transition: width 0.6s cubic-bezier(0.6, 0.04, 0.12, 0.96);
}
.site-loader.anim {
  width: 25%;
}

.logo-middle {
  display: none;
  height: auto;
  margin: 0 auto;
  position: relative;
  text-align: center;
  top: calc(50% - 100px);
  width: 230px;
}
.logo-middle img {
  max-width: 100%;
  height: auto;
  display: block;
}

#gotoTop {
  position: fixed;
  right: 10px;
  bottom: 60px;
  width: 40px;
  height: 40px;
  font-size: 30px;
  line-height: 40px;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  background-size: 80%;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 50%;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 3;
  -webkit-transition: all 0.4s cubic-bezier(0.6, 0.04, 0.12, 0.96);
  -moz-transition: all 0.4s cubic-bezier(0.6, 0.04, 0.12, 0.96);
  -ms-transition: all 0.4s cubic-bezier(0.6, 0.04, 0.12, 0.96);
  -o-transition: all 0.4s cubic-bezier(0.6, 0.04, 0.12, 0.96);
  transition: all 0.4s cubic-bezier(0.6, 0.04, 0.12, 0.96);
}
#gotoTop.active {
  opacity: 1;
  visibility: visible;
}

.landscape-alert {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #ffffff;
  left: 0;
  top: 0;
  text-align: center;
  z-index: 9999;
  display: none;
  padding: 20% 10% 0;
  overflow: hidden;
}
@media only screen and (max-device-width: 700px) and (orientation: landscape) {
  .landscape-alert {
    display: block;
  }
}
.landscape-alert p {
  font-size: 16px;
  line-height: 26px;
  color: #000000;
}
.landscape-alert span {
  margin-top: 10px;
  display: inline-block;
  border: 1px solid #000000;
  padding: 5px 10px;
}

header {
  position: absolute;
  transform: translateY(0);
  width: 100%;
  z-index: z("site-header");
  height: 75px;
  background: #f7f7f7;
  border-bottom: 1px solid #e0e0e0;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
header.top {
  transform: translateY(-100%);
  position: fixed;
  box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0.1);
}
header.sticky {
  transform: translateY(0);
}
header .mobile-menu {
  cursor: pointer;
  float: right;
  height: 32px;
  width: 32px;
  display: none;
  -webkit-transition: left 0.6s ease;
  -moz-transition: left 0.6s ease;
  -ms-transition: left 0.6s ease;
  -o-transition: left 0.6s ease;
  transition: left 0.6s ease;
}
@media only screen and (max-width: 1024px) {
  header .mobile-menu {
    display: block;
  }
}
header .mobile-menu:hover .nav-icon, header .mobile-menu:hover .nav-icon::before, header .mobile-menu:hover .nav-icon::after {
  background: #5D5D5D;
}
header .mobile-menu.active:hover .nav-icon::before, header .mobile-menu.active:hover .nav-icon::after {
  background: #5D5D5D;
}
header .mobile-menu.active .nav-icon {
  -webkit-transition: background 0.6s ease;
  -moz-transition: background 0.6s ease;
  -ms-transition: background 0.6s ease;
  -o-transition: background 0.6s ease;
  transition: background 0.6s ease;
  background: transparent;
}
header .mobile-menu.active .nav-icon::before, header .mobile-menu.active .nav-icon::after {
  top: 0;
  -webkit-transition: transform 0.3s ease;
  -moz-transition: transform 0.3s ease;
  -ms-transition: transform 0.3s ease;
  -o-transition: transform 0.3s ease;
  transition: transform 0.3s ease;
}
header .mobile-menu.active .nav-icon::before {
  -webkit-transform: rotate3d(0, 0, 1, 45deg);
  -moz-transform: rotate3d(0, 0, 1, 45deg);
  -ms-transform: rotate3d(0, 0, 1, 45deg);
  -o-transform: rotate3d(0, 0, 1, 45deg);
  transform: rotate3d(0, 0, 1, 45deg);
}
header .mobile-menu.active .nav-icon::after {
  -webkit-transform: rotate3d(0, 0, 1, -45deg);
  -moz-transform: rotate3d(0, 0, 1, -45deg);
  -ms-transform: rotate3d(0, 0, 1, -45deg);
  -o-transform: rotate3d(0, 0, 1, -45deg);
  transform: rotate3d(0, 0, 1, -45deg);
}
header .nav-icon,
header .nav-icon::before,
header .nav-icon::after {
  background: rgba(0, 0, 0, 0.5);
  display: inline-block;
  width: 100%;
  height: 4px;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
header .nav-icon {
  position: relative;
}
header .nav-icon::before, header .nav-icon::after {
  position: absolute;
  left: 0;
  content: "";
  transform-origin: 50% 50% 0;
  -webkit-transform-origin: 50% 50% 0;
  -moz-transform-origin: 50% 50% 0;
}
header .nav-icon::before {
  top: 10px;
}
header .nav-icon::after {
  top: -10px;
}

.mobile-nav {
  display: none;
  position: fixed;
  height: 100vh;
  right: 0;
  top: 75px;
  width: 50%;
  background: #fefefe;
  z-index: 5;
  box-shadow: 0 0 5px 0 #999;
  -webkit-transform: translateX(120%);
  -moz-transform: translateX(120%);
  -ms-transform: translateX(120%);
  -o-transform: translateX(120%);
  transform: translateX(120%);
  -webkit-transition: transform 500ms cubic-bezier(0.6, 0.04, 0.12, 0.96);
  -moz-transition: transform 500ms cubic-bezier(0.6, 0.04, 0.12, 0.96);
  -ms-transition: transform 500ms cubic-bezier(0.6, 0.04, 0.12, 0.96);
  -o-transition: transform 500ms cubic-bezier(0.6, 0.04, 0.12, 0.96);
  transition: transform 500ms cubic-bezier(0.6, 0.04, 0.12, 0.96);
}
.mobile-nav.active {
  -webkit-transform: translateX(0);
  -moz-transform: translateX(0);
  -ms-transform: translateX(0);
  -o-transform: translateX(0);
  transform: translateX(0);
}
@media only screen and (max-width: 1024px) {
  .mobile-nav {
    display: block;
  }
}

