.container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.blurred-img {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  overflow: hidden;
  padding-bottom: 50%;
}
.blurred-img::before {
  content: "";
  position: absolute;
  inset: 0;
  opacity: 0;
  animation: pulse 2.5s infinite;
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 3;
}
.blurred-img img {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
  position: absolute;
  transition: opacity 250ms ease-in-out;
  opacity: 0;
  z-index: 1;
}
.blurred-img.loaded::before {
  content: none;
}
.blurred-img.loaded img {
  opacity: 1;
}

@keyframes pulse {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.1;
  }
  100% {
    opacity: 0;
  }
}
